import { Component, useState, useEffect} from "react";
import { css } from "emotion";
import { Color } from "reactism/utils";
import { Wrapper } from "reactism/elements";
import { Link } from "Routes";
import { withRouter } from "next/router";

const HeaderTwo = ({ router }) => {

  const [ dropDown, setDropDown ] = useState(false)

  const onClickBurger = () => {
    setDropDown(!dropDown)
  }

  const baseStyle = css`
      background-color: #fff;
      border-bottom: 2px solid rgba(0, 0, 0, 0.1);
      .burgerButton {
        display: none;
        position: absolute;
        top: 1em;
        right: 1em;
        z-index: 11;
        .bar {
          width: 25px;
          height: 3px;
          margin-bottom: 5px;
          background-color: rgba(0, 0, 0, 1);
        }
      }
      .logo {
        display: none;
        position: absolute;
        width: 100%;
        top: -8px;
        left: 1em;
        z-index: 11;
        img {
          width: 40px;
        }
      }
      .wrapper {
        display: grid;
        grid-template-columns: 11.2% 11.2% 11.2% auto auto 12% 12% 12%;
        justify-items: center;
        margin: 0;
        padding: 0;
        a {
          text-decoration: none;
          display: block;
          padding: 0.8rem 0;
          color: #000;
          font-weight: bold;
          transition: 0.2s ease;
          &:hover {
            font-weight: bold;
            color: ${Color.success};
          }
        }

        li {
          list-style: none;
          margin: 0;
          transition: 0.2s ease;
          &.active a {
            color: ${Color.success};
            font-weight: bold;
            border-bottom: 3px solid ${Color.success};
          }
          &:hover:not(.active) {
            border-bottom: 3px solid ${Color.success};
          }
        }

        li:nth-child(1),
        li:nth-child(2),
        li:nth-child(3),
        li:nth-child(4) {
          justify-self: start;
        }
        li:nth-child(5),
        li:nth-child(6),
        li:nth-child(7),
        li:nth-child(8) {
          justify-self: end;
        }
      }

      @media (max-width: 700px) {
        background: #fff;
        height: 55px;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        /* height: 50px; */
        z-index: 10;
        .burgerButton {
          display: block;
        }
        .logo {
          display: grid;
          grid-template-columns: 13% 1fr;
          align-items: center;
          width: 80%;
        }
        .wrapper {
          /* visibility: ${ dropDown }; */
          grid-template-columns: 1fr;
          position: absolute;
          display: block;
          background-color: #fff;
          padding-top: 50px;
          left: 0;
          right: 0;
          padding-left: 1em;
          ul {
            transition: none !important;
            li {
              transition: none;
              a {
                transition: none; 
              }
              &.active a {
                color: ${Color.success};
                border-bottom: 3px solid ${Color.success};
              }
            } 
          }
        }
      }
    `;

    const { pathname } = router;

    return (
      <nav className={baseStyle}>
        <div className="burgerButton" onClick={onClickBurger}>
          <div className="bar" />
          <div className="bar" />
          <div className="bar" />
        </div>
        <div className="logo">
          <img src="/static/logo_kejari_color.svg" alt="" />
          <h2>KEJARI BARRU</h2>
        </div>
        <Wrapper pl={7} pr={7}>
          <ul className={`${
          dropDown
            ? css`visibility: visible`
            : css`visibility: collapse`
          } wrapper`}>
            <li className={pathname === "/home" ? "active" : null}>
              <Link route="/">
                <a>Beranda</a>
              </Link>
            </li>
            <li className={pathname === "/profile" ? "active" : null}>
              <Link route="profil">
                <a>Profil</a>
              </Link>
            </li>
            <li className={pathname === "/tools" ? "active" : null}>
              <Link route="sarana">
                <a>Sarana</a>
              </Link>
            </li>
            <li className={pathname === "/organization" ? "active" : null}>
              <Link route="organisasi">
                <a>Organisasi</a>
              </Link>
            </li>
            <li
              className={
                pathname === "/news" || pathname === "/detail" ? "active" : null
              }
            >
              <Link route="berita">
                <a>Berita</a>
              </Link>
            </li>
            <li className={pathname === "/gallery" ? "active" : null}>
              <Link route="galeri">
                <a>Galeri</a>
              </Link>
            </li>
            <li className={pathname === "/events" ? "active" : null}>
              <Link route="kegiatan">
                <a>Kegiatan</a>
              </Link>
            </li>
            <li className={pathname === "/features" ? "active" : null}>
              <Link route="layanan">
                <a>Layanan</a>
              </Link>
            </li>
          </ul>
        </Wrapper>
      </nav>
    );
  }

export default withRouter(HeaderTwo);
